import * as React from "react";
import { graphql, type HeadFC, type PageProps } from "gatsby";

const block = (block: { type: string | null; text: string | null } | null) => {
  if (!block || !block.text) {
    return <p></p>;
  }

  if (block.type === "paragraph") {
    return <p>{block.text}</p>;
  }

  if (block.type === "heading_1") {
    return <h1>{block.text}</h1>;
  }

  if (block.type === "heading_2") {
    return <h2>{block.text}</h2>;
  }

  if (block.type === "heading_3") {
    return <h3>{block.text}</h3>;
  }

  if (block.type === "quote" || block.type === "callout") {
    return <blockquote>{block.text}</blockquote>;
  }

  if (block.type === "bulleted_list_item") {
    return <li>{block.text}</li>;
  }

  if (block.type === "image") {
    return <img src={block.text} />;
  }

  return <p></p>;
};

const ArticlePage: React.FC<PageProps<Queries.ArticlePageQuery>> = (props) => {
  console.log(props.data);
  return (
    <main>
      <h1>{props.data.article?.name}</h1>
      <p>{props.data.article?.status}</p>
      <p>
        <a href="/">Back</a>
      </p>
      <hr />
      {props.data.article?.blocks?.map(block)}
      <hr />
      <p>
        <a href="/">Back</a>
      </p>
    </main>
  );
};

export default ArticlePage;

export const Head: HeadFC = () => <title>Article Page</title>;

export const query = graphql`
  query ArticlePage($id: String!) {
    article(id: { eq: $id }) {
      id
      name
      status
      blocks {
        type
        text
      }
    }
  }
`;
